<template>
    <main class="app-main">
        <section class="top">
            <p class="title">
                <md-icon class="go-back" name="arrow-left" size="lg" @click="goBack"></md-icon>
                专家智库
                <img class="search" src="../../assets/technology/icon_search@2x.png" alt="" @click="goSearch">
            </p>
        </section>
        <section class="middle">
            <md-drop-menu ref="menu" :data="MenuData" @change="changeMenu"/>
            <div class="area-btn" @click="showMenu">
                <span>地区选择</span>
            </div>
        </section>
        <section class="bottom">
            <div class="md-example-child md-example-child-scroll-view md-example-child-scroll-view-0" :style="{height: Height}">
                <md-scroll-view @end-reached="$_onEndReached" :auto-reflow="true" ref="scrollView" :scrolling-x="false" @scroll="$_onScroll">
                    <div
                            v-for="i in list"
                            class="scroll-view-item"
                            :key="i.id"
                            @click="$_onItemClick(i)">
                        <img :src="i.pic" alt="">
                        <div class="article">
                            <span style="width: 100%" class="name" v-text="i.name"></span>
                            <span style="width: 100%" class="genre" v-text="i.type"></span>
                            <span class="title" v-text="i.genre"></span>
                        </div>
                    </div>
                    <md-scroll-view-more slot="more" :is-finished="isFinished">
                    </md-scroll-view-more>
                </md-scroll-view>
                <md-tab-picker
                        title="所在地区"
                        describe=""
                        large-radius
                        :data="areaData"
                        v-model="show"
                        v-if="show"
                        @select="handleSelect"
                        @change="handleChange"/>
            </div>
        </section>
    </main>
</template>

<script>
import { getList } from '@/apiUrl/expert'
import { getDictionary } from '@/apiUrl/login'
import { getCity, getArea } from '@/apiUrl/base'
import areaData from '../../../static/data'
import { TabBar, Swiper, SwiperItem, ScrollView, ScrollViewMore, DropMenu, Icon, Toast, TabPicker } from 'mand-mobile'
export default {
  name: 'Devices',
  components: {
    [TabBar.name]: TabBar,
    [Swiper.name]: Swiper,
    [SwiperItem.name]: SwiperItem,
    [ScrollView.name]: ScrollView,
    [ScrollViewMore.name]: ScrollViewMore,
    [DropMenu.name]: DropMenu,
    [Icon.name]: Icon,
    [Toast.name]: Toast,
    [TabPicker.name]: TabPicker
  },
  data () {
    return {
      searchParams: {
        pageSize: 10,
        pageNumber: 1,
        genre: '',
        professorType: '',
        province: '',
        city: '',
        area: '',
        provinceId: '',
        cityId: ''
      },
      // tab项
      list: [],
      isFinished: false,
      Height: '900px',
      MenuData: [
        {
          text: '专家类别',
          options: []
        },
        {
          text: '研究领域',
          options: []
        }
      ],
      show: false,
      areaData: areaData
    }
  },
  mounted () {
    this.getList()
    this.getGenre()
    console.log(window.innerHeight)
    this.Height = window.innerHeight - 80 + 'px'
  },
  methods: {
    getList () {
      console.log(this.searchParams)
      getList(this.searchParams).then(res => {
        this.list = res.data
      }).catch(err => {
        Toast.failed(err)
      })
    },
    // 获取所属领域列表
    getGenre () {
      getDictionary({ type: 'professorType' }).then(res => {
        res.data.forEach(item => {
          item.text = item.label
        })
          const args = {
              description: "领域",
              id: "",
              img: "",
              label: "全部",
              text: '全部',
              remarks: "4",
              sort: 1,
              type: "genre",
              value: ""
          }
          res.data.unshift(args)
        this.MenuData[0]['options'] = res.data
      }).catch(err => {
        Toast.failed(err)
      })
      getDictionary({ type: 'genre' }).then(res => {
        res.data.forEach(item => {
          item.text = item.label
        })
        this.MenuData[1]['options'] = res.data
      }).catch(err => {
        Toast.failed(err)
      })
    },
    getSelectedValue (index) {
      console.log(index)
    },
    onSwiperChange (from, to) {
      this.current = to
    },
    // 跳转新闻详情页
    $_onItemClick (i) {
      this.$router.push({
        path: '/expert/detail',
        query: { id: i.id }
      })
    },
    $_onEndReached () {
      if (this.isFinished) {
        return
      }
      this.searchParams.pageNumber++
      getList(this.searchParams).then(res => {
        this.list = this.list.concat(res.data)
        if (res.data.length === 0) {
          this.isFinished = true
        }
        this.$refs.scrollView.finishLoadMore()
      }).catch(err => {
        Toast.failed(err)
      })
    },
    $_onScroll ({ scrollLeft, scrollTop }) {
      // console.log(`[Mand Mobile ScrollView - demo0 - onScroll] scrollLeft: ${scrollLeft}, scrollTop: ${scrollTop}`)
    },
    // 回退
    goBack () {
      this.$router.go(-1)
    },
    // 跳转关键字检索页
    goSearch () {
      this.$router.push('/search/index')
    },
    changeMenu (val) {
      if (this.$refs.menu.getSelectedValue(0)) {
        this.searchParams[this.$refs.menu.getSelectedValue(0).type] = this.$refs.menu.getSelectedValue(0).value
      }
      if (this.$refs.menu.getSelectedValue(1)) {
        this.searchParams[this.$refs.menu.getSelectedValue(1).type] = this.$refs.menu.getSelectedValue(1).value
      }
      this.searchParams.pageNumber = 1
      this.getList()
    },
    showMenu (val) {
      this.show = true
    },
    handleChange (val) {
      val.values.forEach((item, key) => {
        switch (key) {
          case 0: this.searchParams.province = item; break
          case 1: this.searchParams.city = item; break
          case 2: this.searchParams.area = item; break
        }
      })
      this.searchParams.pageNumber = 1
      this.getList()
    },
    handleSelect (data) {
      if (data.option.name === 'province') {
        this.areaData = areaData
        this.provinceId = data.option.selected.index
        getCity({ provinceid: data.option.selected.id }).then(res => {
          this.areaData.options[this.provinceId].children.options = []
          res.data.forEach((item, key) => {
            this.areaData.options[this.provinceId].children.options.push({
              label: item.city,
              value: item.city,
              id: item.cityid,
              index: key,
              children: {
                'name': 'area',
                'label': '请选择',
                'options': [{
                  'value': '',
                  'label': '',
                  'index': 2
                }]
              }
            })
          })
          // this.areaData.options[this.provinceId].children.options.shift(0)
        }).catch(err => {
          Toast.failed(err)
        })
      } else if (data.option.name === 'city') {
        if (data.option.selected) {
          this.cityId = data.option.selected.index
        }
        if (!(data.option.selected && data.option.selected.hasOwnProperty('id'))) {
          return
        }
        getArea({ cityid: data.option.selected.id }).then(res => {
          this.areaData.options[this.provinceId].children.options[this.cityId].children.options = []
          res.data.forEach(item => {
            this.areaData.options[this.provinceId].children.options[this.cityId].children.options.push({
              label: item.area,
              value: item.area,
              id: item.areaid
            })
          })
          // this.areaData.options[this.provinceId].children.options[this.cityId].children.options.shift(0)
        }).catch(err => {
          Toast.failed(err)
        })
      }
    }
  }
}
</script>

<style lang="stylus" scoped>
    .app-main
        padding-top 88px
        height 100%
        margin-bottom 0
        .middle
            height 80px
            .md-drop-menu
                height 80px
                border-bottom 1px solid #eeeeee
                .md-drop-menu-bar
                .bar-item
                    border-right 1px solid #eeeeee
        .bottom
            width 690px
            margin 0 auto
    .scroll-view-container
        margin-top 48px!important
        div
            display flex
            img
                width 180px
                height 180px
                margin  30px
                border-radius 50%
            .article
                width 400px
                text-align left
                margin 25px 0
                display inline-block
                flex-direction column
                p
                    color #333333
                    font-size 28px
                    font-weight Medium
                    text-overflow ellipsis
                    overflow hidden
                    display -webkit-box
                    -webkit-line-clamp 2
                    -webkit-box-orient vertical
                span
                    display inline-block
                    align-items center
                    line-height 24px
                    font-size 24px
                    img
                        width 24px
                        margin-right 10px
                .name
                    margin-top 15px
                    color #333333FF
                    font-size 28px
                    font-weight 500
                .genre
                    margin-top 15px
                    margin-bottom 48px
                    color #666666FF
                    font-size 24px
                .title
                    font-size 24px
                    color #021955FF
                    border 1px solid #021955FF
                    border-radius 8px
                    padding 10px
        .md-scroll-view-more
            justify-content center
    .md-drop-menu
        width 66%
        font-size 28px
        top 88px
        z-index 8
    /deep/ .md-drop-menu-list
        padding-top 170px!important
    .scroll-view-item
        text-align center
        font-size 28px
        font-family DINAlternate-Bold
        height 240px
        margin-bottom 30px
        border-radius 20px
        border 2px solid rgba(2,25,85,0.11);
        label
            position absolute
            color #ffffff
            background:linear-gradient(0deg,rgba(202,2,57,1),rgba(255,97,53,1))
            height 32px
            line-height 32px
            left 8px
            font-size 20px
            font-family PingFang-SC-Regular
            border-radius 0 0 4px 4px
    /deep/.scroll-view-container
            margin-top 36px
    .area-btn
        width 34%
        height 80px
        line-height 80px
        border-bottom 1px solid #eeeeee
        font-size 28px
        color #666666FF
        position fixed
        right 0
</style>
