import request from '../utils/request'
import apiPath from './url'
import qs from 'qs'
import store from '@/store/index'

export function getList (data) {
  const url = store.getters.status ? apiPath.expert.getList : apiPath.expert.getListWithoutToken
  data = qs.stringify(data)
  return request({
    url: url,
    method: 'post',
    data: data
  })
}
export function getDetail (data) {
  const url = store.getters.status ? apiPath.expert.getDetail : apiPath.expert.getDetailWithoutToken
  data = qs.stringify(data)
  return request({
    url: url,
    method: 'post',
    data: data
  })
}
